* { margin:0; padding:0; box-sizing:border-box; }

.App {
  height: 100vh;
  width: 100vw;
  text-align: center;
  background-color: white;
  overflow: hidden;
  transition: -webkit-filter 1s, filter 1s;
}

.App--inverted {
  -webkit-filter: invert(100%);
  filter: invert(100%);
}

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

p {
  font-size: calc(10px + 1vw);
}

.typewrite {
  margin: 0;
  font-weight: 500;
  font-size: calc(10px + 2vw);
  font-family: 'Inter', sans-serif;
}

/* p,h1,h3 {
  font-family: 'Inter', sans-serif;
} */

a { 
  color: #0f0f0f;
  text-decoration: none; }

a:hover {
  color: cyan;
  font-weight: 400;
}

/* selected link */
a:active {
  color: #0f0f0f;
  font-weight: 400;
  text-decoration: underline;
}

.App-header {
  z-index: 1;
  padding: 20px;
  top: 0;
  left: 0;
  position: absolute;
  display: flex;
  flex-direction: row;
  font-size: calc(10px + 0.5vw);
  color: #0f0f0f;
  width: 100%;
}

.App-category-title {
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 20px;
  color: #0f0f0f;
  font-weight: 300;
}

.App-invert {
  height: 40px;
  display: flex;
  cursor: pointer;
  margin-left: auto;
  justify-content: flex-end;
  margin-top: 0;
  margin-bottom: 0;
  color: #0f0f0f;
  object-fit: contain;
}

.App-category-title-clicked {
  margin-top: 0;
  margin-right: 20px;
  color: #0f0f0f;
  font-weight: 400;
  text-decoration: underline;
}

.App-body {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  top: 0;
  left: 0;
  position: absolute;
  display: flex;
  flex-direction: row;
  color: #0f0f0f;

  /* -moz-box-shadow:    inset 0 0 20px #00000021;
  -webkit-box-shadow: inset 0 0 20px #00000021;
  box-shadow:         inset 0 0 20px #00000021; */
}

.App-footer {
  z-index: 1;
  bottom: 0;
  left: 0;
  padding: 20px;
  position: absolute;
  display: flex;
  flex-direction: row;
  font-size: calc(10px + 0.5vw);
  color: #0f0f0f;
  width: 100%;
}